.vpr {
  grid-column: 1 / -1;
  background-color: #fff;
  // padding: 72px 20px;

  // @include pageGrid;

  // @include onTablet {
  //   grid-column: span 6;
  //   // padding: 200px 40px;
  //   // background-image: url(../images/Shapes.png);
  //   // background-position: center;
  //   // background-repeat: no-repeat;
  //   // background-size: contain;
  // }

  @include onDesktop {
    grid-column: 1 / -1;
  }

  &__image {
    padding: 72px 20px;
    grid-column: 1 / -1;
    @include onTablet {
      padding: 200px 50px;
      grid-column: 1 / -1;

      background-image: url(../images/Shapes.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    @include onBiggerDesktop {
      margin-inline: auto;
      max-width: 1260px;
    }
  }

  &__title {
    grid-column: span 2;

    color: $main-title-color;
    text-align: center;
    font-family: "Poppins";
    font-size: 44px;
    font-weight: 600;
    line-height: 52.8px;
    margin-bottom: 24px;

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 1 / -1;
    }
  }

  &__text {
    grid-column: span 2;

    color: $main-text-color;
    text-align: center;
    font-family: "Open Sans";
    font-size: 22px;
    font-weight: 600;
    line-height: 33px;
    margin-bottom: 56px;

    @include onTablet {
      grid-column: span 6;
      margin-inline: auto;
      margin-bottom: 64px;
    }

    @include onDesktop {
      grid-column: 3 / -3;
    }
  }

  &__button {
    height: 50px;
    border-radius: 8px;
    background: #2060F6;
    border: none;
    cursor: pointer;
    width: 100%;
    transition-duration: 0.5s;
    grid-column: span 2;


    @include onTablet {
      width: 264px;
      margin-inline: auto;
      grid-column: 3 / 5;
    }

    @include onDesktop {
      grid-column: 6 / 8;
    }
  }

  &__button--text {
    color: #FFF;
    text-align: center;
    font-family: "Poppins";
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
  }

  &__button:hover {
    transform: scale(1.05);
  }
}
