@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin contentPaddingInLine {
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 41px;
  }

  @include onDesktop {
    padding-inline: 54px;
    max-width: 1260px;
    margin-inline: auto;
  }
}

@mixin onTablet {
  @media (min-width: 576px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin onBiggerDesktop {
  @media (min-width: $biggerDesktop-min-width) {
    @content;
  }
}

@mixin pageGrid {
  --columns: 2;

  display: grid;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
    --columns: 6;
    column-gap: 30px;
  }

  @include onDesktop {
    --columns: 12;
    column-gap: 20px;
  }
}
