.block__container {
  @include pageGrid;
}
.reviews {
  grid-column: span 2;
  padding-inline: 20px;

  @include onTablet {
    padding-inline: 40px;
    grid-column: span 6;
    // column-gap: 0px;       //<------------------
  }

  @include onDesktop {
    padding-inline: 50px;
    grid-column: 1 / -1;
    margin-inline: auto;
    max-width: 1260px;
  }

  &__description {
    grid-column: span 2;
    text-align: left;
    color: #c0Cdd7;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 16px;

    @include onTablet {
      grid-column: span 6;
    }

    @include onDesktop {
      grid-column: 1 / -1;
    }
  }

  &__title {
    text-align: left;
    grid-column: span 2;
    color: $main-title-color;
    font-family: "Poppins";
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
    margin-bottom: 48px;
    width: 200px;

    @include onTablet {
      grid-column: span 2;
    }

    @include onDesktop {
      grid-column: 1 / 23;
    }
  }

  &__block {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: span 2;
    height: 456px;
    background-color: #fff;
    border-radius: 16px;
    box-shadow: 0px 9px 18px 0px rgba(37, 41, 49, 0.03);
    margin-bottom: 24px;
    transition-duration: 0.5s;
    @include onTablet {
      grid-column: span 6;
    }
  }

  &__block:hover {
    transform: scale(1.05);
  }

  &__block--1 {
    @include onDesktop {
      grid-column: 1 / 5;
    }
  }

  &__block--2 {
    @include onDesktop {
      grid-column: 5 / 9;
    }
  }

  &__block--3 {
    @include onDesktop {
      grid-column: 9 / -1;
    }
  }

  &__block:last-child {
    margin-bottom: 0;
  }

  &__block__photo {
    position: relative;
    width: 110px;
    height: 110px;
    border-radius: 50%;
    z-index: 1;
  }


  &__block__comment {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 112px;
    gap: 8px;
    width: 196px;
    margin-bottom: 32px;

    @include onTablet {
      width: 362px;
    }

    @include onDesktop {
      width: auto;
    }
  }

  &__block__comment--marks {
    height: 24px;
    width: 24px;
  }

  &__block__comment--text {
    height: 64px;
    color: $main-text-color;
    text-align: center;
    font-family: "Open Sans";
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
  }

  &__block__container {
    display: flex;
    align-items: center;
    height: 100%;
    position: relative;
  }

  &__block__box--1 {
    top: 65px;
    right: 40px;
    height: 45px;
    width: 110px;
    border-radius: 12px;
    transform: rotate(-60deg);
    background: #2060f6;
    position: absolute;
  }

  &__block__box--2 {
    bottom: 65px;
    right: -40px;
    width: 110px;
    height: 45px;
    border-radius: 12px;
    transform: rotate(-60deg);
    background: #2060f6;
    position: absolute;
  }

  &__block__titles {
    display: flex;
    flex-direction: column;
    gap: 4px;
    margin-bottom: 32px;
  }

  &__block__titles__name {
    color: #253757;
    text-align: center;
    font-family: "Open Sans";
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px
  }

  &__block__titles__words {
    color: #c0cdd7;
    text-align: center;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
  }
}
