.nav-top {
  display: flex;
  justify-content: space-between;
  grid-column: 1 / -1;
  padding: 20px 20px 0;

  @include onTablet {
    padding: 20px 40px 0;
  }

  @include onDesktop {
    box-sizing: border-box;
    padding: 50px 50px 0;
    width: 100%;
    max-width: 1260px;
    margin-inline: auto;
  }

  &__logo {
    display: flex;
    transition-duration: 0.5s;
  }

  &__logo:hover {
    transform: scale(1.05);
  }

  &__icons {
    display: flex;
    align-items: center;
    gap: 32px;
  }

  &__button {
    display: flex;
    background-color: #2c2c2c;
    border: none;
    padding: 0;

    padding-bottom: 8px;
    border-bottom: 2px solid #2060F6;
  }

  &__button--text {
    color: #FFF;
    text-align: center;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    transition-duration: 0.5s;
  }

  &__button--text:hover {
    color: #2060F6;
  }
}
