.main__slider {
  background-image: url(../images/slider/slide-img-3.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 30px;
  height: 390px;
  width: 100%;
  grid-column: span 2;

  @include onTablet {
    grid-column: span 6;
  }

  @include onDesktop {
    position: absolute;
    bottom: 0;
    height: 680px;
    border-radius: 0;
    border-top-left-radius: 30px;
    border-bottom-right-radius: 30px;
  }

  &__botton {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    width: 32px;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    transition-duration: 0.5s;
  }

  &__botton:hover {
    transform: scale(1.1);
  }

  &__botton--1 {
    display: block;
    height: 18px;
    width: 18px;
    background-image: url(../icons/mdi_chevron_left.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__botton--2 {
    display: block;
    height: 18px;
    width: 18px;
    background-image: url(../icons/mdi_chevron_right.svg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__bottons {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;

    @include onDesktop {
      margin: 0;
    }
  }
}

.slide__container {
  padding: 0 20px 24px;
  position: absolute;
  bottom: 0;

  @include onTablet {
    padding: 0 40px 24px;
  }

  @include onDesktop {
    padding-bottom: 48px;
    padding-left: 78px;
    display: flex;
    gap: 24px;
    align-items: center;
  }
}

.description {
  display: flex;
  flex-direction: column;
  gap: 8px;


  &__intro {
    color: #FFF;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__text {
    width: 280px;
    color: #FFF;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 19.6px;
  }

}
