.menu {
  background-color: $header-background;
  box-sizing: border-box;
  height: 100vh;

  @include onDesktop {
    display: none;
  }
}

.menu__nav {
  display: flex;
  justify-content: space-between;
  padding: 20px 20px 0;

  @include onTablet {
    padding: 20px 40px 0;
  }

  &__logo {
    display: flex;
    transition-duration: 0.5s;
  }

  &__logo:hover {
    transform: scale(1.05);
  }

  &__icons {
    display: flex;
    align-items: center;
    gap: 32px;
  }
}

.nav__aside {
  padding: 0;
  padding-inline: 20px;
  margin-top: 80px;

  @include onTablet {
    margin-top: 150px;
    padding-inline: 40px;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 70px;

    @include onTablet {
      gap: 100px;
    }
  }

  &__link {
    color: #FFF;
    font-family: "Poppins";
    font-size: 20px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    transition-duration: 0.5s;


    @include onTablet {
      font-size: 30px;
    }
  }

  &__link:hover {
    color: #2060F6;
  }
}
