.footer {
  grid-column: span 2;
  background-color: $header-background;
  // padding: 72px 20px 80px;


  border-top-right-radius: 30px;
  border-top-left-radius: 30px;

  @include onTablet {
    grid-column: span 6;
    // padding: 180px 40px 80px;
  }

  &__block {
    @include pageGrid;
    grid-column: 1 / -1;
    padding: 72px 20px 80px;

    @include onTablet {
      padding: 180px 40px 80px;
    }

    @include onDesktop {
      padding: 180px 50px 80px;
      grid-column: 1 / -1;
      margin-inline: auto;
      max-width: 1260px;
    }
  }

  &__title {
    grid-column: span 2;

    color: #FFF;
    font-family: "Poppins";
    font-size: 32px;
    font-weight: 600;
    line-height: 41.6px;
    margin-bottom: 48px;

    @include onTablet {
      grid-column: span 6;

    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__title--2 {
    line-height: 48px;


    @include onTablet {
      margin-bottom: 56px;
    }

    @include onDesktop {
      grid-row: 1;
      grid-column: 7 / -1;
    }
  }

  &__form {
    grid-column: span 2;
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;

    @include onTablet {
      grid-column: span 5;
      margin-bottom: 120px;
    }

    @include onDesktop {
      grid-row: 1;
      margin-bottom: 0px;
      grid-column: span 5;
    }
  }

  &__input {
    height: 41px;
    margin-bottom: 40px;
    background-color: $header-background;
    border: none;
    border-bottom: 1px solid #fff;
    cursor:auto;
    transition-duration: 0.5s;

    color: #FFF;
    font-family: "Poppins";
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;

    @include onTablet {
      width: 362px;
    }
  }

  &__input:active {
    border-bottom: 1px solid #144ed4;
  }

  &__input:hover {
    transition-duration: 0.5s;
    border-bottom: 1px solid #144ed4;
  }

  &__input:focus {
    border-bottom: 1px solid #144ed4;
    outline: none;
  }

  &__input::placeholder {
    color: #FFF;
    font-family: "Poppins";
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    opacity: 0.7;
  }

  &__input:nth-child(4) {
    margin-bottom: 56px;

    @include onTablet {
      margin-bottom: 80px;
    }
  }

  &__button {
    height: 50px;
    border-radius: 8px;
    background: #2060F6;
    cursor: pointer;
    border: none;
    width: 100%;
    transition-duration: 0.5s;
    grid-column: span 2;

    @include onTablet {
      width: 264px;
      grid-column: span 3;
    }
  }

  &__button--text {
    color: #FFF;
    text-align: center;
    font-family: "Poppins";
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
  }

  &__button:hover {
    transform: scale(1.05);
  }

  &__contact-us {
    grid-column: span 2;

    @include onTablet {
      grid-column: span 6;
    }

    @include onDesktop {
      grid-row: 1;
      grid-column: 7 / -1;
    }
  }

  &__contact-us__description {
    color: #FFF;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
    opacity: 0.5;
    margin-top: 40px;
    margin-bottom: 8px;

    @include onTablet {
      grid-column: span 6;
      margin-top: 48px;
    }
  }

  &__contact-us__description:nth-child(5) {
    margin-bottom: 16px;

    @include onTablet {
    }
  }

  &__contact-us__description:nth-child(2) {
    margin-top: 0;
  }

  &__contact-us__text {
    color: #FFF;
    font-family: "Open Sans";
    font-size: 24px;
    font-weight: 600;
    line-height: 36px;
    margin-bottom: 40px;
    text-decoration: none;
  }

  &__contact-us__text:hover {
    text-decoration: underline;
  }

  &__contact-us__media {
    display: flex;
    gap: 32px;
    margin-bottom: 80px;

    @include onTablet {
      margin-bottom: 139px;
    }
  }

  &__logo {
    grid-column: span 2;
    transition-duration: 0.5s;
    cursor: pointer;

    @include pageGrid;

    @include onTablet {
      grid-column: span 6;
    }

    @include onDesktop {

      margin-top: 45px;
      grid-column: 1 / 1;
    }
  }

  &__logo:hover {
    transform: scale(1.05);
  }
}

.media__logo {
  cursor: pointer;
  transition-duration: 0.5s;
}

.media__logo:hover {
  transform: scale(1.1);
}
