.about {
  grid-column: span 2;
  padding-inline: 20px;

  @include onTablet {
    grid-column: span 6;
    padding-inline: 40px;
  }

  @include onDesktop {
    grid-column: 3 / 11;
    padding-inline: 5px;
    margin-inline: auto;
    max-width: 1260px;
  }

  &__title {
    color: $main-title-color;
    text-align: center;
    font-family: "Poppins";
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
    // margin-bottom: 24px;

    @include onTablet {
      margin-bottom: 16px;
    }
  }

  &__text {
    color: $main-text-color;
    text-align: center;
    font-family: "Open Sans";
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: 33px;

    @include onTablet {
      font-size: 24px;
      line-height: 36px;
    }
  }
}
