@keyframes increaseSize {
  0% {
    width: 100%;
  }
  100% {
    width: 110%;
  }
}

.services {
  grid-column: span 2;
  padding-inline: 20px;

  @include pageGrid;

  @include onTablet {
    height: 926px;
    grid-column: span 6;
    padding: 28px 40px;
  }

  @include onDesktop {
    grid-column: span 12;
    height: 480px;
    padding: 103px 50px;
    margin-inline: auto;
    max-width: 1260px;
  }

  &__description {
    text-align: left;
    grid-column: span 2;
    color: #c0Cdd7;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 3px;
    text-transform: uppercase;
    margin-bottom: 16px;

    @include onTablet {
      grid-row: 1;
      grid-column: span 6;
    }

    @include onDesktop {
      grid-column: 1 / 3;
    }
  }

  &__title {
    text-align: left;
    grid-column: span 2;
    color: $main-title-color;
    font-family: "Poppins";
    font-size: 32px;
    font-weight: 600;
    line-height: 41.6px;
    margin-bottom: 24px;

    @include onTablet {
      grid-row: 2;
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__text {
    text-align: left;
    grid-column: span 2;
    color: $main-text-color;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    margin-bottom: 48px;
    display: grid;

    @include onTablet {
      grid-row: 3;
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 5;
    }
  }

  &__block {
    box-sizing: border-box;
    padding: 32px;
    grid-column: span 2;
    margin-bottom: 24px;
    border-radius: 16px;
    background-color: #fff;

    box-shadow: 0px 9px 18px 0px rgba(37, 41, 49, 0.03);

    @include onDesktop {
      height: 264px;
      width: 264px;
    }
  }


  &__block:last-child {
    margin-bottom: 0;
  }

  &__block--1 {
    @include onTablet {
      grid-row: 4;
      grid-column: span 3;
      transform: translateY(-100px);
    }

    @include onDesktop {
      grid-column: 7 / 10;
      transform: translateY(-390px);
    }
  }

  &__block--2 {
    @include onTablet {
      grid-row: 3;
      grid-column: 4 / 7;
      transform: translateY(50px);
    }

    @include onDesktop {
      grid-column: 10 / 13;
      transform: translateY(-240px);
    }
  }

  &__block--3 {
    @include onTablet {
      grid-row: 6;
      grid-column: span 3;
      transform: translateY(-350px);
    }

    @include onDesktop {
      grid-row: 6;
      grid-column: 7 / 10;
      transform: translateY(-655px);
    }
  }

  &__block--4 {
    @include onTablet {
      grid-row: 5;
      grid-column: 4 / 7;
      transform: translateY(-230px);
    }

    @include onDesktop {
      grid-column: 10 / 13;
      transform: translateY(-530px);
    }
  }

  &__block__number {
    width: 46px;
    height: 24px;
    border-radius: 23px;
    background-color: rgba(32, 96, 246, 0.1);
    margin-bottom: 24px;

    color: #2060f6;
    text-align: center;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 600;
    line-height: 24px;
    text-transform: uppercase;
  }

  &__block__text {
    text-align: left;
    color: #6c788b;
    font-family: "Open Sans";
    font-size: 13px;
    font-weight: 600;
    line-height: 19.5px;
    margin-bottom: 32px;
  }

  &__block__button {
    cursor: pointer;
    display: flex;
    gap: 16px;
    align-items: center;
    border: none;
    background-color: #fff;
    transition-duration: 0.5s;
  }

  &__block__button:hover .services__block__button--arrow,
  &__block__button:hover .services__block__button--text {
    transform: scale(1.1);
  }

  &__block__button--arrow {
    background-image: url(../icons/mdi_arrow_forward.png);
    background-position: center;
    background-repeat: no-repeat;
    transition-duration: 0.5s;

    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: #2060f6;
  }

  &__block__button--text {
    transition-duration: 0.5s;
    text-decoration: none;
    color: #334563;
    text-align: center;
    font-family: "Poppins";
    font-size: 15px;
    font-weight: 600;
  }
}
