.header {
  background-color: $header-background;
  box-sizing: border-box;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  grid-column: span 2;

  @include onDesktop {
    @include pageGrid;
  }

  &__main {
    padding-inline: 20px;

    @include onTablet {
      padding-inline: 40px;
      @include pageGrid;
    }

    @include onDesktop {
      box-sizing: border-box;
      grid-column: 1 / 7;
      display: block;
      margin-left: auto;
      max-width: 1260px;
      padding-right: 85px;
    }
  }

  &__main--title {
    grid-column: span 2;
    margin-top: 48px;
    color: #FFF;
    font-family: "Poppins";
    font-size: 52px;
    font-style: normal;
    font-weight: 600;
    line-height: 62.4px;
    letter-spacing: -2px;


    @include onTablet {
      grid-column: span 6;
      font-size: 64px;
      line-height: 68px;
    }

    @include onDesktop {
      grid-column: 1 / 4;
      width: 490px;
      margin-top: 164px;
    }
  }

  &__main--text {
    grid-column: span 2;
    color: #FFF;
    font-family: "Open Sans";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    margin-top: 32px;

    @include onTablet {
      grid-column: span 4;
    }

    @include onDesktop {
      width: 362px;
      grid-column: 1 / 6;
    }
  }


  &__button {
    height: 50px;
    border-radius: 8px;
    background: #2060F6;
    border: none;
    width: 100%;
    transition-duration: 0.5s;
    margin-top: 72px;
    grid-column: span 2;
    margin-bottom: 48px;
    cursor: pointer;

    @include onTablet {
      width: 264px;
      grid-column: span 3;
      margin-bottom: 148px;
    }

    @include onDesktop {
      grid-column: 1 / 5;
      width: 264px;
      margin-bottom: 205px;
    }
  }

  &__button:hover {
    transform: scale(1.05);
  }


  &__button--text {
    color: #FFF;
    text-align: center;
    font-family: "Poppins";
    font-size: 15px;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
  }
}

  .header__bottom {
    position: relative;
    grid-column: 1 / -1;

    @include onDesktop {
      grid-column: 7 / -1;
    }
  }
