.expertise {
  grid-column: 1 / -1;
  background-color: #fff;
  border-radius: 30px;
  // padding: 72px 20px;
  @include pageGrid;


  @include onTablet {
    // grid-column: span 6;
    // padding: 128px 40px;
  }

  @include onDesktop {
    // grid-column: span 12;
    // padding: 128px 50px 134px;
    // margin-inline: auto;
    // max-width: 1260px;
  }

  &__container {
    padding: 72px 20px;
    grid-column: 1 / -1;
    @include pageGrid;

    @include onTablet {
    grid-column: 1 / -1;
    padding: 128px 40px;
    }

    @include onDesktop {
      padding: 128px 50px 134px;
    }

    @include onBiggerDesktop {
      margin-inline: auto;
      max-width: 1260px;
    }
  }

  &__title {
    color: $main-title-color;
    text-align: center;
    font-family: "Poppins";
    font-size: 44px;
    font-weight: 600;
    line-height: 52.8px;
    grid-column: span 2;

    @include onTablet {
      grid-column: span 6;
      font-size: 52px;
      line-height: 78px;
    }

    @include onDesktop {
      grid-column: span 12;
    }
  }

  &__text {
    color: $main-text-color;
    text-align: center;
    font-family: "Open Sans";
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
  }

  &__title--h3 {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 16px;
  }

  &__image {
    margin-top: 56px;
    margin-bottom: 32px;
    width: 184px;
    border-radius: 30px;
    transition-duration: 0.5s;

    @include onTablet {
      margin-top: 64px;
    }
  }

  &__block {
    cursor: pointer;
  }

  &__block:hover {
    .expertise__image {
      transform: scale(1.05);
    }
  }

  &__branding {
    grid-column: span 2;

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 1 / 4;
    }
  }

  &__communication {
    grid-column: span 2;

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 5 / 9;
    }
  }

  &__strategy {
    grid-column: span 2;

    @include onTablet {
      grid-column: 2 / 6;
    }

    @include onDesktop {
      grid-column: 10 / 13;
    }
  }
}
