.page {
  font-family: 'Open Sans', sans-serif;
  font-family: 'Poppins', sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    padding: 0;
    background: $c-gray;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    pointer-events: none;
  }
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

* {
  margin: 0;
  padding: 0;
}
