.icon {
  display: block;
  height: 24px;
  width: 24px;
  transition-duration: 0.5s;

  &--menu {
    background-image: url(../icons/burger-menu.svg);
    cursor: pointer;


    @include onDesktop {
      display: none;
    }
  }

  &--menu:hover {
    transform: scale(1.1);
  }

  &--close {
    background-image: url(../icons/icon--close.svg);
  }
}
