.nav {
  margin-top: 48px;
  grid-column: span 2;

  @include onTablet {
    grid-column: 1 / -1;
  }

  @include onDesktop {
    grid-row: 2;
    grid-column: 4 / -1;
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 24px;

    @include onTablet {
      flex-direction: row;
      gap: 40px;
    }
  }

  &__link {
    grid-column: span 2;
    color: #FFF;
    font-family: "Poppins";
    font-size: 13px;
    font-weight: 700;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
    text-decoration: none;
    transition-duration: 0.5s;


    @include onTablet {
      grid-column: span 6;
    }
  }

  &__link:hover {
    color: #2060F6;
  }
}
